@import "../../styles/index.scss";

.App {
  @include size(563px, 1218px);

  background: black;
  color: white;
  border-radius: 50px;
  padding: 20px;

  .top {
    height: 250px;
    padding: 0 20px;
  }
  .display {
    font-size: 130px;
    font-weight: 300;
    text-align: right;
    margin-bottom: 20px;
  }
  .buttons {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
  }
